html,body,p,div,
ul,li{
  padding: 0;
  margin: 0;
}
a{
  text-decoration: none;
}
::-webkit-scrollbar{
  display: none;
}
body,html{
  min-width: 1200px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header {
  height: 80px;
  background: #1c2143;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1001; }
  .header-wrap {
    width: 1200px;
    margin: 0 auto; }
  .header .fr {
    float: right; }
  .header-items {
    cursor: pointer;
    color: #fff;
    font-size: 18px;
    display: inline-block;
    line-height: 80px; }
    .header-items .avatar {
      height: 40px;
      border-radius: 50%;
      vertical-align: middle;
      margin: 0 10px;
      -webkit-transition: 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);
      transition: 0.3s cubic-bezier(0.075, 0.82, 0.165, 1); }
      .header-items .avatar:hover {
        -webkit-transform: scale(1.5);
                transform: scale(1.5); }
    .header-items a {
      margin: 0 10px;
      color: #fff; }
      .header-items a:hover {
        opacity: 0.7; }

.footer {
  height: 70px;
  line-height: 70px;
  background: #1c2143;
  text-align: center;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  font-size: 14px;
  font-style: italic;
  color: #fff; }
  .footer .icon {
    cursor: pointer;
    height: 25px;
    margin-left: 10px;
    vertical-align: middle; }

.win {
  position: fixed;
  cursor: ew-resize;
  bottom: 90px;
  z-index: 1001;
  right: 20px;
  width: 300px;
  box-sizing: border-box;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 3px;
  background: #1c2143;
  opacity: 0.8;
  -webkit-transition: .1s linear;
  transition: .1s linear;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: #fff; }
  .win ul li {
    list-style: none;
    height: 40px;
    line-height: 40px; }
    .win ul li span {
      display: inline-block;
      width: 90px;
      text-align: left; }
  .win .source {
    font-size: 12px;
    color: #ddd;
    text-align: center; }
    .win .source a {
      color: #ddd; }
  .win .toolbar {
    background: #fff; }
    .win .toolbar span {
      display: inline-block;
      width: 12px;
      height: 12px;
      border-radius: 50%;
      margin-right: 10px;
      cursor: -webkit-grab;
      cursor: grab; }
      .win .toolbar span:nth-of-type(1) {
        background: #ed6c61; }
      .win .toolbar span:nth-of-type(2) {
        background: #f6c251; }
      .win .toolbar span:nth-of-type(3) {
        background: #65cb57; }
    .win .toolbar i {
      color: #333; }

.home {
  margin-top: 80px;
  width: 100%;
  background: #484f6e;
  background-size: contain;
  height: 800px;
  display: flex;
  justify-content: center; }
  .home p {
    margin: 80px 20px;
    height: 200px;
    width: 200px;
    border: 1px solid #fff;
    opacity: 0.7;
    border-radius: 5px;
    text-align: center;
    font-size: 14px;
    color: #fff;
    box-sizing: border-box;
    padding: 10px;
    cursor: pointer;
    -webkit-transition: .3s ease-in-out;
    transition: .3s ease-in-out; }
    .home p:hover {
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
      opacity: 1; }

.table {
  margin-top: 100px; }
  .table table {
    background: #484f6e;
    border: 1px solid #ddd;
    margin: 0 auto;
    border-spacing: 0;
    color: #fff; }
    .table table th {
      border-bottom: 1px solid #ddd;
      border-right: 1px solid #ddd;
      padding: 3px;
      width: 120px;
      height: 40px;
      line-height: 40px;
      cursor: pointer; }
    .table table td {
      border-bottom: 1px solid #ddd;
      border-right: 1px solid #ddd;
      padding: 3px;
      width: 120px;
      height: 30px;
      line-height: 30px;
      text-align: center; }
    .table table tr:hover {
      background: #1c2143; }
  .table .pager {
    border: 1px solid #ddd;
    margin: 20px auto 80px;
    color: #fff;
    width: 300px;
    display: flex;
    justify-content: center; }
    .table .pager p {
      display: inline-block;
      background: #484f6e;
      width: 20px;
      height: 20px;
      cursor: pointer;
      text-align: center;
      line-height: 20px;
      margin-left: 5px; }
      .table .pager p:hover {
        background: #1c2143; }
    .table .pager .active {
      background: #fff;
      color: #333; }
      .table .pager .active:hover {
        background: #fff;
        color: #333; }

